import React from "react";

import { Ellipse } from "../../atoms";
import IcoNewsletterForm from "../../organisms/IcoNewsletterForm";
import NavigationIco from "../../organisms/NavigationIco";
import {
  CardInfoBlock,
  CounselorBlock,
  PartnersBlock,
  ProgressBlock,
} from "../../templates";
import BlackFooter from "../IcoPage/BlackFooter";

const BecomeCounselor = () => {
  const progress = [
    "Załóż bezpłatne konto",
    "Podaj 2-3 specjalizacje",
    "Ustal cenę za swoją usługę",
    "Umawiaj wizyty, buduj swój profil i zdobywaj opinie",
    "Wypłacaj środki z platformy na konto bankowe.",
  ];

  return (
    <>
      <NavigationIco />
      <div className={"container"}>
        <div className="logo-ellipse--wrapper">
          <Ellipse isLight={true} />
        </div>
        <CounselorBlock />
        <div className="partners--block">
          <PartnersBlock />
        </div>
        <div className="cards-info--block">
          <CardInfoBlock />
        </div>
        <div className="counselor-how-it-work--block">
          <ProgressBlock
            progress={progress}
            text={"Jak to działa? To bardzo proste:"}
            additionalClassname={"longer"}
          />
        </div>
      </div>
      <IcoNewsletterForm
        text={"Dołącz do naszego newslettera!"}
        label={"Twój email"}
        placeholder={"Email"}
        buttonText={"Dołącz"}
      />
      <BlackFooter />
    </>
  );
};

export default BecomeCounselor;
